@import url(https://fonts.googleapis.com/css2?family=Exo+2&display=swap);

html{overflow-y:scroll}*{margin:0;padding:0;font-family:"Exo 2"}#root{width:80%;margin:0 auto}body{background-color:#d7d7d7}

.faShoppingTruck,.faShoppingTruckInBasket{font-size:24px;color:#5acc58;cursor:pointer;position:absolute;right:0px;transition:-webkit-transform 0.1s ease-in;transition:transform 0.1s ease-in;transition:transform 0.1s ease-in, -webkit-transform 0.1s ease-in}.faShoppingTruck:hover,.faShoppingTruckInBasket:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.faShoppingTruck:active,.faShoppingTruckInBasket:active{color:green;-webkit-transform:scale(2);transform:scale(2)}.faShoppingTruckInBasket{color:#757575;transition:all 1s ease-out;pointer-events:none;opacity:0.3}.faCartMinus,.faCartMinusInBasket{font-size:24px;color:#f35555;cursor:pointer;position:absolute;right:18%}.faCartMinus:hover,.faCartMinusInBasket:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.faCartMinus:active,.faCartMinusInBasket:active{color:red;-webkit-transform:scale(2);transform:scale(2)}.faCartMinusInBasket{color:#757575;transition:all 1s ease;pointer-events:none;opacity:0.3}

.active-class-li{background-color:#8d33da;color:whitesmoke}

